.theme-header {
  background: url(https://www.almondlee.cn/storage/006tKfTcgy1g1om8efw2nj307z04a40f.jpg) no-repeat;
  background-size: 100% 100%;
  height: 20vh;
  filter: blur(4px);
  //box-shadow: 0px 9px 7px 0px rgba(130, 130, 130, 0.4);
}

.header-text {
  font-size: 3em;
  color: white;
  position: absolute;
  top: 5vh;
  left: 10px;
  right: 10px;
  text-align: center;

}

.cell-work-index {
  margin: 5vw 0;
  padding: 4vw;
  background-color: white;
  width: 100%;

  .work-cell-row {
    margin: 2vw;
    display: flex;
    //flex-wrap: nowrap;
    justify-content: space-between;

    .work-name {
      font-size: 1.8em;
      font-weight: 500;
    }

    .work_money {
      color: #fa8163;
      font-weight: 600;
    }

    .work_tpl {
      padding: 1vw;
      border-radius: 5px;
      background-color: #f0f0f0;
      color: #939393;
    }

    .button-more {
      flex-shrink: 0;
    }
  }
}