


.cell-work-index {
  margin: 5vw 0;
  padding: 4vw;
  background-color: white;
  width: 100%;

  .work-cell-row {
    margin: 2vw;
    display: flex;
    //flex-wrap: nowrap;
    justify-content: space-between;

    .work-name {
      font-size: 1.8em;
      font-weight: 500;
    }

    .work_money {
      color: #fa8163;
      font-weight: 600;
    }

    .work_tpl {
      padding: 1vw;
      border-radius: 5px;
      background-color: #f0f0f0;
      color: #939393;
    }

    .button-more {
      flex-shrink: 0;
    }

    .button-margin {
      margin-right: 1vw;
    }

    .name {
      margin-left: 2vw;
    }
  }


